import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { device } from "../styles/variables"
import githubIcon from "../images/icon_github.svg"
import linkedInIcon from "../images/icon_linkedin.svg"

const LinkNameIcon = styled.img`
  display: inline-block;
  margin: 0;
  margin-right: 10px;
  padding: 0;
  text-decoration: none;
  width: 20px;
`

const Container = styled.div`
  margin: 20px 0 40px;
  padding: 0;
  width: 100%;
  text-align: center;

  a {
    color: inherit;
    text-decoration: none;
  }
`

const Title = styled.h1`
color: #444
  display: block;
  font-size: 45px;
  font-weight: 600;
  letter-spacing: 2px;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;

  @media ${device.tablet} {
    font-size: 50px;
  }
`

const isSelected = name =>
  window.location.pathname && window.location.pathname.indexOf(`/${name}/`) > -1

const LinkName = styled.p`
  color: ${props => (isSelected(props.name) ? props.fontColor : "#222")};
  display: inline-block;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 1.4;
  margin: 0;
  margin-right: 10px;
  margin-top: 10px;
  padding: 0;
  text-align: center;
  text-decoration: none;

  @media ${device.tablet} {
    font-size: 18px;
  }
`

export const Header = () => (
  <Container>
    <Link to="/">
      <Title> Jack Murphy </Title>
    </Link>
    <div>
      <Link to="/about/">
        <LinkName name="about" fontColor="#3DA2FF">
          About
        </LinkName>
      </Link>
      <Link to="/blog/">
        <LinkName name="blog" fontColor="#EB5C58">
          Blog
        </LinkName>
      </Link>
      <a href="https://github.com/murpjack" rel="noreferrer" target="_blank">
        <LinkNameIcon src={githubIcon} />
      </a>
      <a
        href="https://www.linkedin.com/in/jack-murphy-bb0426155/"
        rel="noreferrer"
        target="_blank"
      >
        <LinkNameIcon src={linkedInIcon} />
      </a>
    </div>
  </Container>
)

export default Header

